/*
Golden ratio typescale
https://type-scale.com/?size=16&scale=1.618&text=A%20Visual%20Type%20Scale&font=Libre%20Franklin&fontweight=900&bodyfont=body_font_default&bodyfontweight=400&lineheight=1.75&backgroundcolor=%23ffffff&fontcolor=%23000000&preview=false
*/
html {
  font-size: 100%;
  height: 100%;
}

body,
html {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Libre Franklin", sans-serif;
  font-weight: 400;
  color: #434343;
  min-height: 100%;
  word-wrap: break-word;
  font-style: normal;
  line-height: 1.5;
}

p {
  margin-bottom: 1rem;
  line-height: 1.5;
}

h1, h2, h3, h4, h5 {
  margin: 3rem 0 1.38rem;
  font-weight: 900;
  line-height: 1.3;
}

h1 {
  margin-top: 0;
  font-size: 6.854rem;
}

h2 {
  font-size: 4.236rem;
}

h3 {
  font-size: 2.618rem;
}

h4 {
  font-size: 1.618rem;
}

h5 {
  font-size: 1rem;
}

small,
.text_small {
  font-size: 0.618rem;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  margin-bottom: 0.5em;
  line-height: 0.875;
}

strong {
  font-weight: 900;
}

header.site-header {
  margin-top: 3.3rem;
}

footer.site-footer {
  background-color: #434343;
  color: #ffff;
}
footer.site-footer aside:nth-child(1) div.wp-block-group {
  display: flex;
  justify-content: space-between;
  padding: 2rem 0 1rem;
}
footer.site-footer aside:nth-child(1) div.wp-block-group ul.wp-block-social-links {
  margin: 0;
}
footer.site-footer aside:nth-child(1) div.wp-block-group ul.wp-block-social-links li.wp-social-link {
  height: 2.25rem;
}
footer.site-footer aside {
  padding: 0 1rem;
}
footer.site-footer aside h4 {
  margin: 0;
  width: 50%;
}
footer.site-footer aside h5 {
  margin: 0;
  font-weight: 200;
}
@media (min-width: 720px) {
  footer.site-footer aside {
    padding: 0 2.875rem;
  }
  footer.site-footer aside:nth-child(1) {
    padding-top: 2.875rem;
  }
  footer.site-footer aside h4 {
    width: auto;
    font-size: 2.618rem;
  }
  footer.site-footer aside h5 {
    font-size: 1.618rem;
  }
}
footer.site-footer aside.widget_yikes_easy_mc_widget form.yikes-easy-mc-form {
  margin: 1rem 0;
}
footer.site-footer aside.widget_yikes_easy_mc_widget form.yikes-easy-mc-form label span {
  font-weight: 500;
}
footer.site-footer aside.widget_yikes_easy_mc_widget form.yikes-easy-mc-form label:nth-child(5) {
  display: none;
}
footer.site-footer aside.widget_yikes_easy_mc_widget form.yikes-easy-mc-form button {
  margin: 0 !important;
}
@media (min-width: 720px) {
  footer.site-footer aside.widget_yikes_easy_mc_widget form.yikes-easy-mc-form {
    display: flex;
    justify-content: space-between;
  }
  footer.site-footer aside.widget_yikes_easy_mc_widget form.yikes-easy-mc-form label {
    margin: 0 0.5rem;
  }
  footer.site-footer aside.widget_yikes_easy_mc_widget form.yikes-easy-mc-form label:nth-child(1) {
    margin-left: 0;
  }
  footer.site-footer aside.widget_yikes_easy_mc_widget form.yikes-easy-mc-form button {
    margin: 0.5rem !important;
  }
}
footer.site-footer ul#footer-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 1rem 5rem;
  padding: 1rem 0;
  border-top: solid #ffff 1px;
}
@media (min-width: 720px) {
  footer.site-footer ul#footer-menu {
    flex-direction: row;
    margin: 0 2.875rem 5rem;
  }
}
footer.site-footer ul#footer-menu > li {
  list-style: none;
}
footer.site-footer ul#footer-menu > li > a {
  font-size: 1.25rem;
  font-weight: 900;
  color: #ffff;
  text-decoration: none;
}
footer.site-footer ul#footer-menu > li ul.sub-menu {
  list-style: none;
  padding-left: 0;
}
footer.site-footer ul#footer-menu > li ul.sub-menu a {
  color: #ffff;
  text-decoration: none;
}

a {
  color: #20A78C;
}
a:hover {
  text-decoration: none;
}

input[type=text], input[type=email] {
  background-color: rgba(91, 203, 181, 0.1) !important;
  border: 1px solid #219653 !important;
  border-radius: 6px;
  height: 2.5rem;
}

button[type=submit] {
  background-color: #FF4630;
  color: #ffff;
  font-weight: 900;
  display: flex;
  align-items: center;
  text-align: center;
  border: transparent solid 1px;
  border-radius: 6px;
}
button[type=submit].yikes-easy-mc-submit-button {
  height: 2.5rem;
  align-self: flex-end;
}